<template>
  <v-card class="accident-dialog">
    <v-dialog v-model="dialog" persistent max-width="500px" min-width="300px">
      <v-card>
        <v-card-title><h2>Confirmar</h2></v-card-title>
        <v-card-text>
          ¿Realmente desea eliminar este siniestro?
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click.stop="dialog = false">Cancelar</v-btn>
          <v-btn color="warning" flat @click.stop="deleteAccident">Aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-toolbar dark class="secondary">
      <v-btn icon @click.native="$emit('close-dialog', false)" dark>
        <v-icon>close</v-icon>
      </v-btn>
      <v-toolbar-title>Siniestro</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn fab dark flat @click.native="dialog = true">
          <v-icon>delete</v-icon>
          <span class="hidden-sm-and-down">Eliminar</span>
        </v-btn>
        <v-btn fab dark flat :to="{name: 'NewAccident'}">
          <v-icon>mode_edit</v-icon>
          <span class="hidden-sm-and-down">Editar</span>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-card-text class="pa-0">
      <v-layout>
        <v-flex>
          <v-card class="grey darken-4 white--text">
            <v-btn color="primary" fab class="policy-type">
              <v-icon>{{accident.policy.policy_type.icon}}</v-icon>
            </v-btn>
            <v-card-text class="white--text">
              <h2><strong>Compañia:</strong> {{accident.policy.insurance.name}}.</h2>
              <h3><strong>Nro:</strong> {{accident.policy.cod_number}}.</h3>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-layout wrap fill-height class="pa-3">
        <v-flex xs12><ga-status :checks="accidentStatus"></ga-status></v-flex>

        <v-flex xs12 class="text-xs-center">
          <h3><strong>FECHA DEL INCIDENTE:</strong> {{accident.date | moment("DD/MM/YYYY")}}</h3>
          <div>
            <h3 class="text-xs-center"><strong>DESCRIPCIÓN:</strong></h3>
            <h3>{{accident.description}}</h3>
          </div>
          <div v-show="accident.location">
            <h3 class="text-xs-center"><strong>LUGAR:</strong></h3>
            <h3>{{accident.location}}</h3>
          </div>

        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
  import GaStatus from '../partial/GAStatus'
  import { VDialog } from 'vuetify'
  import { VCard, VCardTitle, VCardText, VCardActions } from 'vuetify/es5/components/VCard'

  export default {
    data () {
      return {
        dialog: false
      }
    },
    computed: {
      accident () {
        return this.$store.state.selectedAccident
      },
      accidentStatus () {
        return [
          {label: 'En Revisión', checked: (this.accident.status === 'p'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}},
          {label: 'Pagado', checked: (this.accident.status === 'a'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}}
        ]
      }
    },
    methods: {
      deleteAccident () {
        this.dialog = false
        this.$store.commit('deleteAccident', this.accident)
        this.$store.dispatch('delFriend', {accident: this.accident, api_token: this.$store.state.user.api_token})
        this.$store.state.selectedAccident = null
        this.$emit('close-dialog', false)
      }
    },
    components: {
      GaStatus,
      VCard,
      VCardText,
      VCardActions,
      VCardTitle,
      VDialog
    }
  }
</script>

<style lang="stylus" scoped>
  h3
    font-weight normal
  .btn.policy-type
    position: absolute !important
    right: 0% !important
    top: -6px !important
</style>



