<template>
  <section class="login-page">
    <v-snackbar
      :timeout="5000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar"
      color="secundary"
    >
      ¡Mensaje!
      <v-btn flat color="primary" @click.native="snackbar = false">Cerrar</v-btn>
    </v-snackbar>

    <v-layout wrap class="pa-3">
      <v-form v-model="valid">
        <v-flex xs12 class="text-xs-center">
          <img src="../../assets/images/logo-slogan.png" alt="logo" class="img-responsive logo">
        </v-flex>
        <v-flex xs12>
          <h5>Ingresa tu correo electrónico para recuperar tu contraseña.</h5>
          <v-text-field
            label="Correo Electrónico"
            min="10"
            max="100"
            required
            :rules="EmailRules"
            type="email"
            v-model="email"
            counter
          ></v-text-field>
          <v-btn :to="{name: 'Login'}">
            Cancelar
          </v-btn>
          <v-btn :disabled="!valid" color="primary">
            Recuperar
          </v-btn>
        </v-flex>
      </v-form>
    </v-layout>
  </section>
</template>

<script>
  import { EmailRules } from '../../validator'
  import { VForm, VTextField, VSnackbar } from 'vuetify'

  export default {
    data () {
      return {
        EmailRules,
        valid: false,
        snackbar: false,
        email: '',
        loading: false
      }
    },
    methods: {
    },
    components: {
      VForm,
      VTextField,
      VSnackbar
    }
  }
</script>



