<template>
  <section class="new-bank-page">
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar.show"
      color="secundary"
    >
      <h5 v-for="(error, index) in snackbar.errors" :key="index">{{error}}</h5>
      <v-btn flat color="primary" @click.native="snackbar.show = false">Cerrar</v-btn>
    </v-snackbar>

    <ga-simple-header>
      <v-btn slot="backButton" icon :to="{name: 'Wallet'}" dark>
        <v-icon>close</v-icon>
      </v-btn>
      <span v-text="headerTitle"></span>
    </ga-simple-header>

    <v-layout class="pa-3">
      <v-form v-model="valid" @submit.prevent="addBank">
        <v-flex xs12>
          <v-select
            v-bind:items="banks"
            v-model="request.bank"
            label="Banco"
            item-text="name"
            :prepend-icon="bankIcon"
            required
            :rules="[
              (v) => !!v || 'La banco es obligatorio',
            ]"
            autocomplete
          >
            <template slot="item" slot-scope="data">
              <template>
                <v-list-tile-avatar>
                  <v-icon>{{data.item.icon}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>
          </v-select>
        </v-flex>
        <v-flex xs12>
          <v-select
            :items="account_types"
            v-model="request.type"
            label="Tipo de cuenta"
            item-text="text"
            item-value="value"
            required
            :rules="RequiredRules('El tipo de cuenta es obligatorio')"
            single-line
            bottom
          ></v-select>
        </v-flex>
        <v-flex xs12>
          <v-text-field
            :prefix="bankCode"
            required
            type="number"
            label="Numero de cuenta"
            v-model="request.account_number"
            :rules="BankAccountNumRules"
          ></v-text-field>
        </v-flex>
        <v-btn
          fixed
          bottom
          right
          large
          fab
          type="submit"
          :disabled="!valid"
        >
          <v-icon>save</v-icon>
        </v-btn>
      </v-form>
    </v-layout>
  </section>
</template>

<script>
  import { VForm, VTextField, VSelect, VAvatar, VSnackbar } from 'vuetify'
  import { RequiredRules, BankAccountNumRules } from '../../validator'
  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        RequiredRules,
        BankAccountNumRules,
        valid: false,
        snackbar: {
          show: false,
          errors: null
        },
        request: {
          api_token: this.$store.state.user.api_token,
          bank: null,
          type: null,
          account_number: 0
        },
        account_types: [
          {text: 'Ahorro', value: 'a'},
          {text: 'Corriente', value: 'c'}
        ]
      }
    },
    mounted () {
      if (this.selectedBank) this.request = this.selectedBank
    },
    computed: {
      selectedBank () {
        return this.$store.state.selectedBank
      },
      banks () {
        return this.$store.state.banks
      },
      headerTitle () {
        let title = (this.selectedBank) ? 'Editar' : 'Nueva'
        return title + ' Cuenta'
      },
      bankCode () {
        return (this.request.bank && this.request.bank.code) ? this.request.bank.code : ''
      },
      bankIcon () {
        return (this.request.bank) ? this.request.bank.icon : 'cancel'
      }
    },
    methods: {
      addBank () {
        this.$store.dispatch('setBankAccount', this.request).then(
          response => {
            if (response.ok && response.status === 200) {
              this.request.id = response.data.id
              this.$store.commit('addBank', this.request)
              this.$router.back()
            }
          }
        ).catch(
          error => {
            this.loading = false
            if (error.status === 422) {
              this.snackbar.errors = error.data
              this.snackbar.show = true
            }
          }
        )
      }
    },
    components: {
      GaSimpleHeader,
      VSnackbar,
      VForm,
      VTextField,
      VSelect,
      VAvatar
    }
  }
</script>



