// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

// Vue
import Vue from 'vue'
import VueResource from 'vue-resource'
import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  VProgressCircular,
  transitions
} from 'vuetify'
import '../node_modules/vuetify/src/stylus/app.styl'
import directives from 'vuetify/src/directives'
import VueMoment from 'vue-moment'

// App
import App from './App'
import appConfig from './config'
// Routes
import router from './router'
// State Manage
import {store} from './store/store'
// Styles
import './assets/stylus/index.styl'

// Uses
Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VList,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    VProgressCircular,
    transitions
  },
  theme: {
    primary: '#ff6d2c',
    secondary: '#212121', // '#00796B',
    accent: '#CCCCCC',
    error: '#ff6d2c',
    info: '#B2DFDB',
    success: 'green',
    warning: 'red'
  }
})
Vue.use(directives)
Vue.use(VueResource)
Vue.use(VueMoment)

// Global Filters
Vue.filter('formatCod', function (value) {
  return value.split('_')[1]
})

// Vue Configs
Vue.config.productionTip = false

// Requests
Vue.http.interceptors.push((request, next) => {
  request.url = appConfig.api_url + request.url
  next()
})

// Session
let session
if (window.localStorage.guiaseguro) session = JSON.parse(window.localStorage.guiaseguro)

// Middlewares
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    if (!session && !session.user.api_token) next(false)
  }
  next()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})



