<template>
  <section class="login-page">
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar"
      color="secundary"
    >
      ¡Correo o contraseña inválidos, Por favor verifica los datos e inténtalo nuevamente!
      <v-btn flat color="primary" @click.native="snackbar = false">Cerrar</v-btn>
    </v-snackbar>

    <v-layout wrap class="pa-3">
      <v-form v-model="valid" @submit.prevent="login">
        <v-flex xs12 class="text-xs-center">
          <img src="../../assets/images/logo-slogan.png" alt="logo" class="img-responsive logo">
        </v-flex>
        <v-flex xs12>
          <v-text-field
            label="Correo Electrónico"
            min="10"
            max="100"
            required
            :rules="EmailRules"
            type="email"
            v-model="email"
            counter
          ></v-text-field>
          <v-text-field
            label="Introduce tu contraseña"
            hint="Al menos 8 caracteres"
            v-model="password"
            min="8"
            required
            :rules="RequiredRules('La contraseña es obligatoria')"
            :append-icon="e1 ? 'visibility' : 'visibility_off'"
            :append-icon-cb="() => (e1 = !e1)"
            :type="e1 ? 'password' : 'text'"
            counter
          ></v-text-field>
          <v-btn small :to="{name: 'ResetPass'}" :disabled="loading">¿Olvido la Contraseña?</v-btn>
          <v-btn color="primary" dark to="Register" :disabled="loading">Registrarse</v-btn>
          <v-btn type="submit" :disabled="!valid || loading" :loading="loading">Entrar</v-btn>
        </v-flex>
        <v-flex xs12 class="text-xs-center">
          <img src="../../assets/images/login.png" alt="login" class="img-responsive">
        </v-flex>
      </v-form>
    </v-layout>

    <v-footer absolute class="text-sm-center text-md-center text-xs-center text-lg-center">&copy; 2017, Guia Seguro C.A</v-footer>
  </section>
</template>

<script>
  import { RequiredRules, EmailRules } from '../../validator'
  import { VForm, VTextField, VSnackbar, VProgressCircular } from 'vuetify'

  export default {
    data () {
      return {
        RequiredRules,
        EmailRules,
        valid: false,
        snackbar: false,
        email: '',
        password: '',
        e1: true,
        loading: false
      }
    },
    mounted () {
      this.$store.commit('logout')
    },
    methods: {
      login () {
        this.loading = true
        this.$store.dispatch('login', {email: this.email, password: this.password})
          .then(
            response => {
              this.loading = false
              if (response.ok && response.status === 200 && response.data.api_token) {
                this.$store.commit('login', response.data)
                this.$store.dispatch('getMasters')
                this.$router.push({name: 'Main'})
              }
            }
          ).catch(
            () => {
              this.snackbar = true
              this.loading = false
            }
          )
      }
    },
    components: {
      VForm,
      VTextField,
      VSnackbar,
      VProgressCircular
    }
  }
</script>



