<template>
  <section class="new-accident-page">
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar.show"
      color="secundary"
    >
      <h5 v-for="(error, index) in snackbar.errors" :key="index">{{error}}</h5>
      <v-btn flat color="primary" @click.native="snackbar.show = false">Cerrar</v-btn>
    </v-snackbar>

    <ga-simple-header>
      <v-btn slot="backButton" icon :to="{name: 'Main', params: {tab: 'accidents'}}" dark>
        <v-icon>close</v-icon>
      </v-btn>
      <span v-text="headerTitle"></span>
    </ga-simple-header>

    <v-layout wrap class="pa-3">
      <v-form v-model="valid" @submit.prevent="addAccident">
        <v-flex xs12>
          <v-select
            v-bind:items="policies"
            v-model="request.policy"
            label="Póliza usada"
            item-text="alias"
            item-value="id"
            return-object
            :prepend-icon="policyIcon"
            cache-items
            required
            :rules="RequiredRules('La póliza es obligatoria')"
            autocomplete
          >
            <template slot="item" slot-scope="data">
              <template>
                <v-list-tile-avatar>
                  <!--<img :src="'/static/' + data.item.insurance.logo" alt="policy">-->
                  <v-icon v-text="data.item.insurance.icon"></v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.alias + ' - ' + data.item.insurance.name"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>
          </v-select>
        </v-flex>
        <v-flex xs12>
          <div class="input-group text-xs-center">
            <label>Fecha y hora del incidente</label>
          </div>
        </v-flex>

        <v-flex xs12>
          <v-menu
            lazy
            :close-on-content-click="false"
            v-model="dateMenu"
            transition="scale-transition"
            max-width="290px"
          >
            <v-text-field
              slot="activator"
              label="Selecciona una fecha"
              v-model="request.date"
              required
              prepend-icon="event"
              readonly
            ></v-text-field>
            <v-date-picker
              :allowed-dates="allowedDates"
              color="secondary"
              locale="es-ES"
              v-model="request.date"
              autosave
            >
            </v-date-picker>
          </v-menu>
        </v-flex>
        <v-flex xs12>
          <v-menu
            lazy
            :close-on-content-click="false"
            v-model="timeMenu"
            transition="scale-transition"
          >
            <v-text-field
              slot="activator"
              required
              label="Selecciona la hora"
              v-model="request.hour"
              prepend-icon="access_time"
              readonly
            ></v-text-field>
            <v-time-picker color="secondary" locale="es-ES" v-model="request.hour" autosave></v-time-picker>
          </v-menu>
        </v-flex>

        <v-flex>
          <v-text-field
            v-model="request.description"
            label="Breve descripción del incidente"
            :rules="[
              (v) => v && v.length < 100 || 'La descripcion debe contener menos de 100 caracteres',
            ]"
            textarea
          ></v-text-field>
        </v-flex>
        <v-flex v-if="manualLocation">
          <v-text-field
            v-model="request.location"
            label="Direccion completa"
            :rules="[
              (v) => v && v.length < 100 || 'La direccion debe contener menos de 100 caracteres',
            ]"
            textarea
          ></v-text-field>
        </v-flex>
        <v-btn
          fixed
          bottom
          right
          large
          fab
          type="submit"
          :disabled="!valid"
        >
          <v-icon>save</v-icon>
        </v-btn>
      </v-form>
    </v-layout>

    <div class="speed-dial-center">
      <v-speed-dial
        v-model="fabMedia"
        direction="top"
        transition="slide-y-reverse-transition"
      >
        <v-btn
          slot="activator"
          dark
          fab
          large
          v-model="fabMedia"
        >
          <v-icon>photo</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
        <v-btn
          fab
          dark
          color="green"
        >
          <v-icon>photo_camera</v-icon>
          <span class="speed-dial-btn-text">Tomar Foto</span>
        </v-btn>
        <v-btn
          fab
          dark
          color="indigo"
        >
          <v-icon>panorama</v-icon>
          <span class="speed-dial-btn-text">Abrir galería</span>
        </v-btn>
      </v-speed-dial>
    </div>

    <v-speed-dial
      v-model="fabLocation"
      bottom
      left
      fixed
      direction="top"
      transition="slide-y-reverse-transition"
    >
      <v-btn
        slot="activator"
        dark
        fab
        large
        v-model="fabLocation"
      >
        <v-icon>add_location</v-icon>
        <v-icon>close</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        color="green"
        @click.native="manualLocation = true; request.location = ''"
      >
        <v-icon>edit_location</v-icon>
        <span class="speed-dial-btn-text">Escribir Dirección</span>
      </v-btn>
      <v-btn
        fab
        dark
        color="indigo"
        @click.native="manualLocation = false"
      >
        <v-icon>add_location</v-icon>
        <span class="speed-dial-btn-text">Usar locación del dispositivo</span>
      </v-btn>
    </v-speed-dial>
  </section>
</template>

<script>
  import { VForm, VDatePicker, VTimePicker, VMenu, VTextField, VSelect, VAvatar, VSpeedDial, VSnackbar } from 'vuetify'
  import { RequiredRules } from '../../validator'
  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        RequiredRules,
        fabLocation: false,
        fabMedia: false,
        valid: false,
        dateMenu: false,
        timeMenu: false,
        snackbar: {
          show: false,
          errors: null
        },
        manualLocation: false,
        request: {
          api_token: this.$store.state.user.api_token,
          policy: null,
          date: null,
          hour: null,
          description: '',
          location: {
            full: '',
            latitude: 0,
            longitude: 0
          }
        }
      }
    },
    created () {
      if (this.selectedAccident) this.request = this.selectedAccident
    },
    computed: {
      selectedAccident () {
        return this.$store.state.selectedAccident
      },
      policies () {
        return this.$store.state.user.policies
      },
      headerTitle () {
        let title = (this.selectedAccident && this.selectedAccident.id) ? 'Editar' : 'Nuevo'
        return title + ' Siniestro'
      },
      policyIcon () {
        return (this.request.policy) ? this.request.policy.policy_type.icon : 'cancel'
      },
      allowedDates () {
        let now = new Date()
        return {
          min: null,
          max: now.toISOString().substr(0, 10)
        }
      }
    },
    watch: {
      fabLocation (newValue) {
        if (newValue) this.fabMedia = false
      },
      fabMedia (newValue) {
        if (newValue) this.fabLocation = false
      }
    },
    methods: {
      addAccident () {
        this.$store.dispatch('setAccident', this.request).then(
          response => {
            if (response.ok && response.status === 200) {
              this.request.id = response.data.id
              this.request.status = response.data.status
              this.$store.commit('addAccident', this.request)
              this.$router.back()
            }
          }
        ).catch(
          error => {
            this.loading = false
            if (error.status === 422) {
              this.snackbar.errors = error.data
              this.snackbar.show = true
            }
          }
        )
      }
    },
    components: {
      GaSimpleHeader,
      VSnackbar,
      VForm,
      VDatePicker,
      VTimePicker,
      VMenu,
      VTextField,
      VSelect,
      VAvatar,
      VSpeedDial
    }
  }
</script>



