<script>
  export default {
    data () {
      return {
        subMenu: {
          x: 0,
          y: 0,
          show: false,
          items: [{title: 'Editar', method: 'editData'}, {title: 'Eliminar', method: 'deleteData'}]
        },
        dialog: false
      }
    },
    methods: {
      subMenuAction (method) {
        this[method]()
      },
      toggleSubMenu (e) {
        e.preventDefault()
        this.subMenu.x = e.clientX
        this.subMenu.y = e.clientY
        this.subMenu.show = !this.subMenu.show
      },
      showDialog (val) {
        this.$store.commit('clearSelecteds')
        this.dialog = arguments[0]
      }
    }
  }
</script>



