<template>
  <section class="polices-tab">
    <v-dialog v-if="selectedPolicy" v-model="dialog" fullscreen scrollable transition="dialog-bottom-transition" :overlay="false">
      <policy @close-dialog="showDialog(arguments[0])"></policy>
    </v-dialog>

    <v-menu offset-y v-model="subMenu.show" :position-absolutely="true" :position-x="subMenu.x" :position-y="subMenu.y">
      <v-list>
        <v-list-tile v-for="item in subMenu.items" :key="item.title" @click.native="subMenuAction(item.method)">
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>

    <v-layout>
      <v-flex class="text-xs-center">
        <img src="/static/policies-top.png" alt="policies" class="top-responsive">
      </v-flex>
    </v-layout>

    <v-layout wrap>
      <div v-if="policies.length < 1">
        <v-flex xs12 class="text-xs-center">
          <h3>¡Empieza a ganar dinero con tus pólizas!</h3>
          <p>
            Te otorgaremos un bonos de <strong>Bsf. {{policy_value}}</strong> por cada póliza que registres.
            También tendrás una agenda detallada de tus pólizas, notificaciones de renovación y
            ofertas de productos adaptados para ti
          </p>
          <p>
            No todos los tipos de póliza optan por el bono, para mayor información contactenos en la sección de soporte
          </p>
        </v-flex>
      </div>
      <v-flex xs12>
        <v-list v-if="policies && policies.length > 0" three-line>
          <template v-for="(policy, index) in policies">
            <v-list-tile avatar
                         :ripple="false"
                         v-bind:key="policy.title"
                         @click.native="selectPolicy(policy,index)"
            >
              <v-list-tile-content>
                <v-list-tile-title><strong>{{ policy.alias }} <small class="grey--text lighten-5">{{(policy.id) ? '' : '● No sincronizada'}}</small></strong></v-list-tile-title>
                <v-list-tile-sub-title class="grey--text text--darken-4">{{ policy.insurance.name }}</v-list-tile-sub-title>
                <v-list-tile-sub-title class="grey--text text--darken-4">{{ policy.cod_number | formatCod }}</v-list-tile-sub-title>
                <v-list-tile-sub-title>{{ policy.status == 'p' ? 'Pendiente por recaudos' : 'Aprobada'  }}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-avatar>
                <v-icon>{{ policy.policy_type.icon}}</v-icon>
              </v-list-tile-avatar>
            </v-list-tile>
            <v-divider v-if="index + 1 < policies.length"></v-divider>
          </template>
          <v-list-tile :ripple="false"></v-list-tile>
        </v-list>
        <p v-else class="text-xs-center">
          ¡No tienes ninguna <strong>póliza</strong> agregada! usa el botón
          <v-btn color="primary"
                 small
                 fab
                 class="elevation-1"
                 :to="{name: 'NewPolicy'}"
          >
            <v-icon>icon-new-policy</v-icon>
          </v-btn>
          para agregar una nueva póliza
        </p>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import { VDialog, VMenu, VAvatar, VDivider } from 'vuetify'
  import genericTab from './Generic.vue'
  import Policy from '../Dialogs/Policy.vue'

  export default {
    mixins: [genericTab],
    data () {
      return {}
    },
    computed: {
      selectedPolicy () {
        return this.$store.state.selectedPolicy
      },
      policies () {
        return this.$store.state.user.policies
      },
      policy_value () {
        return this.$store.state.options.point_for_policy
      }
    },
    methods: {
      selectPolicy (policy, index) {
        policy.$index = index
        this.$store.commit('selectPolicy', policy)
        this.dialog = true
      }
    },
    components: {
      Policy,
      VDialog,
      VMenu,
      VAvatar,
      VDivider
    }
  }
</script>

<style>

</style>



