<template>
  <v-card class="transfer-dialog">
    <v-toolbar dark class="secondary">
      <v-btn icon @click.native="$emit('close-dialog', false)" dark>
        <v-icon>close</v-icon>
      </v-btn>

      <v-toolbar-title>Transferir</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn flat @click.native="addTransfer" :disabled="!valid">Solicitar</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-layout wrap class="pa-3">
      <v-form v-model="valid">
        <v-flex xs12>
          <v-select
            v-bind:items="bank_accounts"
            v-model="request.bank_account"
            label="Cuenta Bancaria"
            item-text="bank.name"
            required
            autocomplete
          >
            <template slot="item" slot-scope="data">
              <template>
                <v-list-tile-avatar>
                  <!--<img :src="'static/' + data.item.bank.logo" alt="">-->
                  <v-icon>{{data.item.bank.icon}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{data.item.bank.name}} - <small>{{data.item.type === 'a' ? 'Ahorro': 'Corriente'}}</small>
                  </v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>
          </v-select>
        </v-flex>
        <v-flex>
          <v-text-field
            min="0"
            :max="credits"
            required
            type="number"
            label="Monto a solicitar"
            v-model="request.amount"
            :rules="[(v) => (v <= credits) || 'El monto a solicitar no puede ser mayor al disponible']"
          ></v-text-field>
        </v-flex>
      </v-form>
    </v-layout>
  </v-card>
</template>

<script>
  import { VCard, VSelect, VTextField, VAvatar, VForm } from 'vuetify'
  export default {
    data () {
      return {
        valid: false,
        request: {
          api_token: this.$store.state.user.api_token,
          bank_account: null,
          amount: 0
        }
      }
    },
    computed: {
      credits () {
        return this.$store.getters.userCredits
      },
      bank_accounts () {
        return this.$store.state.user.bank_accounts
      }
    },
    methods: {
      addTransfer () {
        this.$store.dispatch('setTransaction', this.request).then(
          response => {
            if (response.ok && response.status === 200) {
              this.request.created_at = response.data.created_at
              // this.request.status = response.data.status
              this.request.status = 'p'
              this.$store.commit('addTransaction', this.request)
            }
          }
        )
        this.$emit('close-dialog', false)
      }
    },
    components: {
      VForm,
      VCard,
      VSelect,
      VTextField,
      VAvatar
    }
  }
</script>



