<template>
  <section class="wallet-page">
    <v-dialog v-model="dialog" persistent v-if="canAddTransaction">
      <ga-transfer-dialog @close-dialog="dialog = arguments[0]"></ga-transfer-dialog>
    </v-dialog>

    <v-dialog v-model="confirmDialog" persistent max-width="500px" min-width="300px">
      <v-card>
        <v-card-title><h2>Confirmar</h2></v-card-title>
        <v-card-text>
          ¿Realmente desea eliminar esta cuenta bancaria?
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click.stop="dialog = false">Cancelar</v-btn>
          <v-btn color="warning" flat @click.stop="deleteBank">Aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <ga-simple-header>Mi Cartera</ga-simple-header>

    <v-tabs
      dark
      color="secondary"
      class="white--text"
      centered
      v-model="activeTab"
    >
      <v-tab v-for="(tab, index) in tabs"
             :key="index"
             :href="'#tab-' + index"
      >
        {{tab.title}}
      </v-tab>
    </v-tabs>

    <v-flex>
      <v-card class="grey darken-4 white--text">
        <v-card-text>
          <h2><strong>Titular:</strong> {{ user.full_name }}.</h2>
          <h3><strong>C.I:</strong> {{ user.identification }}.</h3>
          <h4><strong>Correo:</strong> {{ user.email }}.</h4>
          <h4 v-show="user.phone"><strong>Teléfono:</strong> {{ user.phone }}.</h4>
        </v-card-text>
      </v-card>
      <div class="text-xs-center black--text">
        <p>
          <span class="title">Monto acumulado:</span>
        </p>
        <p>
          <span class="display-1">
            <strong>
              <span v-if="$store.state.options.symbol_position === 'l'" v-text="$store.state.options.money_symbol + ' ' + credits"></span>
              <span v-else v-text="credits + ' ' + $store.state.options.money_symbol"></span>
            </strong>
          </span>
        </p>
      </div>
    </v-flex>

    <v-tabs-items v-model="activeTab">
      <!--Tab Bancos-->
      <v-tab-item id="tab-0">
        <v-layout>
          <v-flex xs12 v-if="user.bank_accounts && user.bank_accounts.length > 0">
            <v-list three-line>
              <template v-for="(bank_account, index) in user.bank_accounts">
                <v-list-tile
                  :ripple="false"
                  :key="bank_account.title"
                  @click.native="selectBank(bank_account, index)"
                >
                  <v-list-tile-avatar>
                    <v-icon>{{bank_account.bank.icon}}</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>
                      {{bank_account.bank.name}}
                    </v-list-tile-title>
                    <v-list-tile-title>
                      <span class="hidden-md-and-down">Nº Cuenta: </span>
                      <strong>N°:</strong>{{ bank_account.account_number }}
                    </v-list-tile-title>
                    <v-list-tile-sub-title><strong>Cuenta:</strong> {{ bank_account.type == 'a' ? 'Ahorro': 'Corriente'}}</v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-btn icon :to="{name: 'NewBank'}">
                      <v-icon color="grey lighten-1">edit</v-icon>
                    </v-btn>
                    <v-btn icon @click.native="confirmDialog = true">
                      <v-icon color="grey lighten-1">delete</v-icon>
                    </v-btn>
                  </v-list-tile-action>
                </v-list-tile>
                <v-divider v-if="index + 1 < user.bank_accounts.length"></v-divider>
              </template>
              <v-list-tile :ripple="false"></v-list-tile>
            </v-list>
          </v-flex>
          <v-flex v-else xs12 class="text-xs-center">
            <img src="/static/bank-account-top.png" height="150">
            <p>
              Aquí podrás agregar tus cuentas bancarias para poder retirar tu dinero cuando gustes.
            </p>
            <p>Recuerda todas las cuentas bancarias agregadas usaran los datos que colocaste al momento del registro.</p>
            <p class="text-xs-center">
              ¡No tienes cuentas bancarias agregadas, para poder hacer una transacción es necesario tener al menos una cuenta! usa el botón
              <v-btn color="primary"
                     small
                     fab
                     class="elevation-1"
                     :to="{name: 'NewBank'}"
              >
                <v-icon>icon-new-bank</v-icon>
              </v-btn>
              para agregar una cuenta nueva
            </p>
          </v-flex>
        </v-layout>
      </v-tab-item>
      <!--Tab Transacciones-->
      <v-tab-item id="tab-1">
        <div v-if="!user.transactions || user.transactions.length < 1">
          <v-flex xs12 class="text-xs-center">
            <img src="/static/transaction-top.png" height="150">
            <p>Transfiere tu dinero acumulado a tus cuentas bancarias cuando gustes.</p>
            <p>Recuerda que las transacciones podrán tardar unos dias dependiendo del banco.</p>
          </v-flex>
          <p v-show="activeFab.disabled" class="text-xs-center">
            No tienes cuentas bancarias a las cuales podamos hacer la transferencia por favor agrega una cuenta primero para continuar...
            <br>
            puedes usar el botón
            <v-btn color="primary"
                   small
                   fab
                   class="elevation-1"
                   :to="{name: 'NewBank'}"
            >
              <v-icon>icon-new-bank</v-icon>
            </v-btn>
            para agregar una cuenta nueva
          </p>
        </div>
        <v-layout v-else>
          <v-flex>
            <h3 class="text-xs-center"><strong>Solicitudes de transferencias realizadas</strong></h3>
            <v-list three-line>
              <template v-for="(transaction, index) in user.transactions">
                <v-list-tile
                  :ripple="false"
                  :key="transaction.id"
                  @click.native="selectBank(transaction, index)"
                >
                  <v-list-tile-avatar>
                    <v-icon>{{transaction.bank_account.bank.icon}}</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>
                      {{transaction.bank_account.bank.name}} ● {{transaction.bank_account.type == 'a' ? 'Ahorro' : 'Corriente'}}
                    </v-list-tile-title>
                    <v-list-tile-title>
                      <strong>Solicitud:</strong>{{ transaction.created_at }} ● {{transaction.status}}
                    </v-list-tile-title>
                    <v-list-tile-sub-title><strong>Monto:</strong> {{ transaction.amount }} <strong>Bsf.</strong></v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-divider v-if="index + 1 < user.transactions.length"></v-divider>
              </template>
            </v-list>
          </v-flex>
        </v-layout>
      </v-tab-item>
    </v-tabs-items>

    <v-fab-transition>
      <v-btn color="primary"
             fixed
             bottom
             right
             large
             fab
             :disabled="activeFab.disabled"
             @click.native="fabAction"
             :key="activeFab.icon"
      >
        <v-icon>{{activeFab.icon}}</v-icon>
      </v-btn>
    </v-fab-transition>
  </section>
</template>

<script>
  import {
    VDialog,
    VAvatar,
    VDivider
  } from 'vuetify'

  import {
    VTabs,
    VTab,
    VTabsItems,
    VTabItem
  } from 'vuetify/es5/components/VTabs'

  import {
    VCard,
    VCardText,
    VCardTitle,
    VCardActions
  } from 'vuetify/es5/components/VCard'

  import GaTransferDialog from '../dialogs/Transfer.vue'
  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        activeTab: null,
        tabs: [
          {'title': 'Cuentas Bancarias'},
          {'title': 'Transacciones'}
        ],
        dialog: false,
        confirmDialog: false
      }
    },
    computed: {
      credits () {
        return this.$store.getters.userCredits
      },
      selectedBank () {
        return this.$store.state.selectedBank
      },
      user () {
        return this.$store.state.user
      },
      canAddTransaction () {
        let canAddTransaction = false
        if (this.$store.state.user.bank_accounts) canAddTransaction = (this.user.bank_accounts.length > 0 && this.credits > 0) && navigator.onLine
        return canAddTransaction
      },
      activeFab () {
        switch (this.activeTab) {
          case 'tab-1':
            return {'class': 'indigo', icon: 'local_atm', dialog: true, disabled: !this.canAddTransaction}
          case 'tab-0':
            return {'class': 'red', icon: 'icon-new-bank', redirect: 'NewBank', disabled: !navigator.onLine}
          default:
            return {}
        }
      }
    },
    methods: {
      selectBank (bank, index) {
        bank.$index = index
        this.$store.commit('selectBank', bank)
      },
      deleteBank () {
        this.confirmDialog = false
        this.$store.commit('deleteBank', this.selectedBank.$index)
        this.$store.dispatch('delBankAccount', {bank: this.selectedBank, api_token: this.user.api_token})
        this.$store.state.selectedPolicy = null
      },
      fabAction () {
        if (this.activeFab.dialog) {
          this.dialog = true
        } else {
          this.$router.push({name: this.activeFab.redirect})
        }
      }
    },
    components: {
      GaTransferDialog,
      GaSimpleHeader,
      VDialog,
      VTabs,
      VTab,
      VTabsItems,
      VTabItem,
      VAvatar,
      VCard,
      VCardText,
      VCardTitle,
      VCardActions,
      VDivider
    }
  }
</script>

<style lang="stylus" scoped>
  h1,h2,h3,h4
    font-weight normal
</style>



