<template>
  <section class="config-page">
    <ga-simple-header>Configuraciones</ga-simple-header>

    <v-layout>
      <v-flex class="text-xs-center">
        <img src="/static/config-top.png" alt="accidents" class="top-responsive">
      </v-flex>
    </v-layout>

    <v-layout>
      <v-flex xs12>
        <v-list two-line subheader>
          <template>
            <div>
              <v-subheader>Notificaciones del APP</v-subheader>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-switch v-model="config.policy_notifications"></v-switch>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Pólizas</v-list-tile-title>
                  <v-list-tile-sub-title>muestra notificaciones al aceptar una póliza y al ser rechazarla</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-switch v-model="config.friend_notifications"></v-switch>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Amigos</v-list-tile-title>
                  <v-list-tile-sub-title>muestra notificaciones cuando un amigo referido termina el registro y cuando agrega su primera póliza</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-switch v-model="config.accident_notifications"></v-switch>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Siniestros</v-list-tile-title>
                  <v-list-tile-sub-title>muestra notificaciones cuando un siniestro fue resuelto</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-switch v-model="config.transfer_notifications"></v-switch>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Transferencias</v-list-tile-title>
                  <v-list-tile-sub-title>muestra notificaciones cuando una transferencia fue realizada o fue rechazada</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </div>
          </template>
        </v-list>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import GaSimpleHeader from '../partial/GaSimpleHeader'
  import { VSwitch, VSubheader } from 'vuetify'

  export default {
    data () {
      return {}
    },
    computed: {
      config () {
        return this.$store.state.user.config
      }
    },
    components: {
      GaSimpleHeader,
      VSwitch,
      VSubheader
    }
  }
</script>



