<template>
  <section class="new-friend-page">
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar.show"
      color="secundary"
    >
      <h5 v-for="(error, index) in snackbar.errors" :key="index">{{error}}</h5>
      <v-btn flat color="primary" @click.native="snackbar.show = false">Cerrar</v-btn>
    </v-snackbar>

    <ga-simple-header>
      <v-btn slot="backButton" icon :to="{name: 'Main', params: {tab: 'friends'}}" dark>
        <v-icon>close</v-icon>
      </v-btn>
      <span v-text="headerTitle"></span>
    </ga-simple-header>

    <v-layout wrap class="pa-3">
      <v-form v-model="valid" @submit.prevent="addFriend">
        <v-flex xl12>
          <v-text-field
            v-model="request.full_name"
            label="Nombre y Apellido"
            required
            :rules="FullNameRules"
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            label="Numero telefónico"
            v-model="request.phone"
            :rules="PhoneRules"
          >
          </v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            label="Correo electrónico"
            v-model="request.email"
            type="email"
            :rules="EmailRules"
          ></v-text-field>
        </v-flex>
        <v-btn
          fixed
          bottom
          right
          large
          fab
          type="submit"
          :disabled="!valid"
        >
          <v-icon>save</v-icon>
        </v-btn>
      </v-form>
    </v-layout>

    <v-speed-dial
      v-model="fabInvitation"
      bottom
      left
      fixed
      direction="top"
      transition="slide-y-reverse-transition"
    >
      <v-btn
        slot="activator"
        dark
        :disabled="!valid"
        fab
        large
        v-model="fabInvitation"
      >
        <v-icon>contacts</v-icon>
        <v-icon>close</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        color="orange"
      >
        <v-icon>textsms</v-icon>
        <span class="speed-dial-btn-text">Mensaje de Texto</span>
      </v-btn>
      <v-btn
        fab
        dark
        color="green"
      >
        <v-icon>share</v-icon>
        <span class="speed-dial-btn-text">Whatsapp</span>
      </v-btn>
      <v-btn
        fab
        dark
        color="indigo"
      >
        <v-icon>share</v-icon>
        <span class="speed-dial-btn-text">Otros</span>
      </v-btn>
    </v-speed-dial>
  </section>
</template>

<script>
  import { VForm, VTextField, VAvatar, VSpeedDial, VSnackbar } from 'vuetify'
  import { FullNameRules, EmailRules, PhoneRules } from '../../validator'
  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        FullNameRules,
        EmailRules,
        PhoneRules,
        fabInvitation: false,
        valid: false,
        snackbar: {
          show: false,
          errors: null
        },
        request: {
          api_token: this.$store.state.user.api_token,
          full_name: '',
          phone: '',
          email: '',
          photo: '/src/assets/images/no-photo.png',
          status: 'p'
        }
      }
    },
    created () {
      if (this.selectedFriend) this.request = this.selectedFriend
    },
    computed: {
      selectedFriend () {
        return this.$store.state.selectedFriend
      },
      headerTitle () {
        let title = (this.selectedFriend) ? 'Editar' : 'Nuevo'
        return title + ' Amigo'
      }
    },
    methods: {
      addFriend () {
        this.$store.dispatch('setFriend', this.request).then(
          response => {
            if (response.ok && response.status === 200) {
              this.$store.commit('addFriend', response.data)
              this.$router.back()
            }
          }
        ).catch(
          error => {
            this.loading = false
            if (error.status === 422) {
              this.snackbar.errors = error.data
              this.snackbar.show = true
            }
          }
        )
      }
    },
    components: {
      GaSimpleHeader,
      VSnackbar,
      VForm,
      VTextField,
      VAvatar,
      VSpeedDial
    }
  }
</script>



