<template>
  <section class="new-policy-page">
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar.show"
      color="secundary"
    >
      <h5 v-for="(error, index) in snackbar.errors" :key="index">{{error}}</h5>
      <v-btn flat color="primary" @click.native="snackbar.show = false">Cerrar</v-btn>
    </v-snackbar>

    <ga-simple-header>
      <v-btn slot="backButton" icon :to="{name: 'Main', params: {tab: 'policies'}}" dark>
        <v-icon>close</v-icon>
      </v-btn>
      <span v-text="headerTitle"></span>
    </ga-simple-header>

    <v-layout wrap class="pa-3">
      <v-form v-model="valid" @submit.prevent="addPolicy">
        <v-flex>
          <v-text-field
            v-model="request.alias"
            label="Alias"
            required
            :rules="AliasRules"
          ></v-text-field>
        </v-flex>
        <v-flex xs12>
          <v-select
            v-bind:items="policies"
            v-model="request.insurance"
            label="Aseguradora"
            item-text="name"
            item-value="id"
            return-object
            :prepend-icon="policyIcon"
            cache-items
            required
            :rules="RequiredRules('La aseguradora es obligatoria')"
            autocomplete
          >
            <template slot="item" slot-scope="data">
              <template>
                <v-list-tile-avatar>
                  <!--<img :src="'/static/' + data.item.logo" alt="policy">-->
                  <v-icon>{{data.item.icon}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>
          </v-select>
        </v-flex>
        <v-flex xs12>
          <v-select
            v-bind:items="policy_types"
            v-model="request.policy_type"
            label="Tipo de póliza"
            item-text="name"
            item-value="id"
            return-object
            :prepend-icon="policyTypeIcon"
            cache-items
            required
            :rules="RequiredRules('El tipo de póliza es obligatorio')"
            autocomplete
          >
            <template slot="item" slot-scope="data">
              <template>
                <v-list-tile-avatar>
                  <v-icon>{{data.item.icon}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>
          </v-select>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="request.cod_number"
            placeholder="Ej: 3213573587"
            label="Numero de Póliza"
            required
            :rules="PolicyNumRules"
          ></v-text-field>
        </v-flex>
        <v-btn
          fixed
          bottom
          right
          large
          fab
          type="submit"
          :disabled="!valid"
        >
          <v-icon>save</v-icon>
        </v-btn>
      </v-form>
    </v-layout>
  </section>
</template>

<script>
  import { VForm, VTextField, VSelect, VAvatar, VSnackbar } from 'vuetify'
  import { RequiredRules, AliasRules, PolicyNumRules } from '../../validator'
  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        RequiredRules,
        AliasRules,
        PolicyNumRules,
        valid: false,
        snackbar: {
          show: false,
          errors: null
        },
        request: {
          api_token: this.$store.state.user.api_token,
          cod_number: null,
          insurance: {name: ''},
          policy_type: null,
          status: 'p',
          alias: ''
        }
      }
    },
    mounted () {
      if (this.selectedPolicy) {
        this.request = this.selectedPolicy
      }
    },
    computed: {
      selectedPolicy () {
        return this.$store.state.selectedPolicy
      },
      policies () {
        return this.$store.state.insurances
      },
      policy_types () {
        return this.$store.state.policy_types
      },
      headerTitle () {
        let title = (this.selectedPolicy) ? 'Editar' : 'Nueva'
        return title + ' Póliza'
      },
      policyIcon () {
        return (this.request.insurance.icon) ? this.request.insurance.icon : 'cancel'
      },
      policyTypeIcon () {
        return (this.request.policy_type) ? this.request.policy_type.icon : 'cancel'
      }
    },
    methods: {
      addPolicy () {
        this.$store.dispatch('setPolicy', this.request).then(
          response => {
            if (response.ok && response.status === 200) {
              this.request.id = response.data.id
              this.request.status = response.data.status
              this.$store.commit('addPolicy', this.request)
              this.$store.state.selectedPolicy = {}
              this.$router.back()
            }
          }
        ).catch(
          error => {
            this.loading = false
            if (error.status === 422) {
              this.snackbar.errors = error.data
              this.snackbar.show = true
            }
          }
        )
      }
    },
    components: {
      GaSimpleHeader,
      VSnackbar,
      VForm,
      VTextField,
      VSelect,
      VAvatar
    }
  }
</script>



