      <v-flex xs12>
        <h3>¿Que desea hacer?</h3>
      </v-flex>
      <v-flex xs12>
        <v-btn flat :to="{name: 'NewPolicy'}" color="green">Añadir póliza</v-btn>
      </v-flex>
      <v-flex xs12>
        <v-btn flat :to="{name: 'ContractPolicy'}" color="primary">Contratar nueva póliza</v-btn>
      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
  import {VCard} from 'vuetify'
  export default {
    data () {
      return {}
    },
    components: {
      VCard
    }
  }
</script>



