<template>
  <section class="register-page">
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar.show"
      color="secundary"
    >
      <h5 v-for="(error, index) in snackbar.errors" :key="index">{{error}}</h5>
      <v-btn flat color="primary" @click.native="snackbar.show = false">Cerrar</v-btn>
    </v-snackbar>

    <v-layout wrap class="pa-3">
      <v-flex xs12 class="text-xs-center">
        <img src="src/assets/images/logo-slogan.png" alt="logo" height="100px" class="img-responsive logo">
      </v-flex>

      <v-form v-model="allStepsValid">
        <v-flex xs12>
          <v-stepper v-model="step">
            <v-stepper-header>
              <v-stepper-step step="1" :complete="stepOneValid">
                Correo electrónico
                <small v-show="!stepOneValid">correo electrónico invalido</small>
              </v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step step="2" :complete="stepTwoValid">
                Datos Personales
                <small v-show="!stepTwoValid">nombre o teléfono celular invalido</small>
              </v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step step="3" :complete="stepThreeValid">
                Seguridad
                <small v-show="!stepThreeValid">contraseña invalida</small>
              </v-stepper-step>
            </v-stepper-header>
            <v-stepper-content step="1">
              <v-form v-model="stepOneValid">
                <v-text-field
                  label="Correo Electrónico"
                  min="10"
                  max="100"
                  v-model="email"
                  type="email"
                  required
                  :rules="EmailRules"
                  counter
                ></v-text-field>
              </v-form>
              <v-btn color="primary" @click.native="step = 2">Continuar</v-btn>
            </v-stepper-content>
            <v-stepper-content step="2">
              <v-form v-model="stepTwoValid">
                <v-layout wrap>
                  <v-flex xs3 >
                    <v-select
                      :items="account_types"
                      v-model="identification_type"
                      class="text-xs-right"
                      required
                      :rules="RequiredRules('El tipo de identificacion es obligatorio')"
                      single-line
                      bottom
                    ></v-select>
                  </v-flex>
                  <v-flex xs9>
                    <v-text-field
                      label="Cédula de identidad o Rif"
                      min="10"
                      max="30"
                      v-model="identification"
                      type="number"
                      required
                      :rules="IdentificationRules"
                    ></v-text-field>
                  </v-flex>
                </v-layout>
                <v-text-field
                  label="Nombre completo"
                  min="10"
                  max="30"
                  v-model="full_name"
                  required
                  :rules="FullNameRules"
                ></v-text-field>
                <v-text-field
                  label="Numero de Teléfono"
                  mask="phone"
                  min="10"
                  max="30"
                  v-model="phone"
                  required
                  :rules="PhoneRules"
                  type="phone"
                ></v-text-field>
                <v-btn @click.native="--step">Anterior</v-btn>
                <v-btn color="primary" @click.native="step = 3">Continuar</v-btn>

              </v-form>
            </v-stepper-content>
            <v-stepper-content step="3">
              <v-form v-model="stepThreeValid">
                <v-text-field
                  label="Introduce tu contraseña"
                  hint="Al menos 8 caracteres"
                  v-model="password"
                  min="8"
                  :append-icon="e1 ? 'visibility' : 'visibility_off'"
                  :append-icon-cb="() => (e1 = !e1)"
                  :type="e1 ? 'password' : 'text'"
                  required
                  :rules="PasswordRules"
                  counter
                ></v-text-field>
                <v-text-field
                  label="Repite tu contraseña"
                  hint="Al menos 8 caracteres"
                  v-model="password_confirmation"
                  min="8"
                  :append-icon="e2 ? 'visibility' : 'visibility_off'"
                  :append-icon-cb="() => (e2 = !e2)"
                  :type="e2 ? 'password' : 'text'"
                  required
                  :rules="PasswordConfirmRules(password)"
                  counter
                ></v-text-field>
                <v-btn @click.native="--step">Anterior</v-btn>
                <v-btn color="primary" @click.native="register" :disabled="!allStepsValid || loading" :loading="loading">Registrar</v-btn>
              </v-form>
            </v-stepper-content>
          </v-stepper>
        </v-flex>
      </v-form>

      <v-flex xs12>
        <v-btn block to="/" :disabled="loading">Cancelar registro</v-btn>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import { RequiredRules, EmailRules, IdentificationRules, PasswordRules, PasswordConfirmRules, FullNameRules, PhoneRules } from '../../validator'
  import { VForm, VDivider, VTextField, VSnackbar, VSelect } from 'vuetify'

  import {
    VStepper,
    VStepperHeader,
    VStepperStep,
    VStepperItems,
    VStepperContent
  } from 'vuetify/es5/components/VStepper'

  export default {
    data () {
      return {
        RequiredRules,
        EmailRules,
        IdentificationRules,
        PasswordRules,
        PasswordConfirmRules,
        FullNameRules,
        PhoneRules,
        allStepsValid: false,
        stepOneValid: false,
        stepTwoValid: false,
        stepThreeValid: false,
        snackbar: {
          show: false,
          errors: null
        },
        account_types: ['V', 'E', 'J'],
        identification_type: 'V',
        identification: null,
        full_name: null,
        email: null,
        phone: null,
        password: null,
        password_confirmation: null,
        e1: true,
        e2: true,
        step: 0,
        loading: false
      }
    },
    watch: {
      step (newValue) {
        if (newValue === 2 && !this.full_name && this.email) {
          console.log('buscar persona')
        }
      }
    },
    methods: {
      register () {
        this.loading = true
        let request = {identification_type: this.identification_type, identification: this.identification, email: this.email, password: this.password, password_confirmation: this.password_confirmation, full_name: this.full_name, phone: this.phone}
        this.$http.post('/register', request)
          .then(
            response => {
              this.loading = false
              if (response.ok && response.data.api_token && response.status === 200) {
                this.$store.commit('login', response.data)
                this.$store.dispatch('getMasters')
                this.$router.push({name: 'Main'})
              }
            }
          ).catch(
            error => {
              this.loading = false
              if (error.status === 422) {
                this.snackbar.errors = error.data
                this.snackbar.show = true
              }
            }
          )
      }
    },
    components: {
      VForm,
      VSnackbar,
      VStepper,
      VStepperHeader,
      VStepperStep,
      VStepperItems,
      VStepperContent,
      VDivider,
      VTextField,
      VSelect
    }
  }
</script>



