<template>
  <div class="status">
    <!--<div class="dashed"></div>-->
    <v-layout wrap>
      <v-flex v-for="(item, index) in checks" :key="index" class="text-xs-center" :class="[columSize]">
        <v-btn :class="[{'btn--small': small, 'btn--large': large}, [(item.checked) ? item.colors.checked : item.colors.unchecked]]" fab :ripple="false" class="elevation-0 white--text">
          <v-icon>{{(item.checked) ? item.icons.checked : item.icons.unchecked}}</v-icon>
        </v-btn>
        <h4>{{item.label}}</h4>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex><hr></v-flex>
    </v-layout>
  </div>
</template>

<script>
  export default {
    data () {
      return {}
    },
    created () {
      if (this.small) this.large = false
      if (this.large) this.small = false
    },
    props: {
      small: {
        type: Boolean,
        default: this.size
      },
      large: {
        type: Boolean,
        default: this.size
      },
      checks: {
        type: Array,
        default () {
          return [
            {label: 'Opcion 1', checked: true, icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}},
            {label: 'Opcion 2', checked: false, icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}}
          ]
        }
      }
    },
    computed: {
      columSize () {
        let size = 12 / this.checks.length
        return 'xs' + size
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .status
    margin .5rem 0
    h4
      font-weight normal
      text-transform: uppercase;
    hr
      margin .5rem 0
    .dashed
      border 6px dashed #757575
      border-left none
      border-right none
      border-bottom none
</style>



