import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginPage from '../components/pages/Login'
import RegisterPage from '../components/pages/Register'
import ResetPass from '../components/pages/ResetPass'
import MainPage from '../components/pages/Main'
import ConfigPage from '../components/pages/Config'
import WalletPage from '../components/pages/Wallet'
import AccountPage from '../components/pages/Account'
import SupportPage from '../components/pages/Support'
import NewPolicyPage from '../components/pages/NewPolicy'
import ContractPolicyPage from '../components/pages/ContractPolicy'
import NewFriendPage from '../components/pages/NewFriend'
import NewAccidentPage from '../components/pages/NewAccident'
import NewBankPage from '../components/pages/NewBank'

Vue.use(VueRouter)

export default new VueRouter({
  mode: 'history',
  routes: [
    { path: '*', component: LoginPage },
    {
      path: '/',
      name: 'Login',
      component: LoginPage
    },
    {
      path: '/register',
      name: 'Register',
      component: RegisterPage
    },
    {
      path: '/reset/password',
      name: 'ResetPass',
      component: ResetPass
    },
    {
      path: '/main/:tab?',
      name: 'Main',
      component: MainPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/wallet',
      name: 'Wallet',
      component: WalletPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/config',
      name: 'Config',
      component: ConfigPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/account',
      name: 'Account',
      component: AccountPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/support',
      name: 'Support',
      component: SupportPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/new/policy',
      name: 'NewPolicy',
      component: NewPolicyPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/contract/policy',
      name: 'ContractPolicy',
      component: ContractPolicyPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/new/friend',
      name: 'NewFriend',
      component: NewFriendPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/new/accident',
      name: 'NewAccident',
      component: NewAccidentPage,
      meta: {requiresAuth: true}
    },
    {
      path: '/new/bank',
      name: 'NewBank',
      component: NewBankPage,
      meta: {requiresAuth: true}
    }
  ]
})



