import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from 'vue-resource'
import VuejsStorage from 'vuejs-storage'

Vue.use(Vuex)
Vue.use(VueResource)
Vue.use(VuejsStorage)

export const store = new Vuex.Store({
  state: {
    user: {},
    banks: [],
    insurances: [],
    policy_types: [],
    options: [],
    selectedPolicy: null,
    selectedFriend: null,
    selectedAccident: null,
    selectedBank: null
  },
  mutations: {
    login (state, user) {
      state.user = user
    },
    logout (state) {
      state.user = {}
    },
    addPolicy (state, newPolicy) {
      if (!state.user.policies) state.user.policies = []
      state.user.policies.push(newPolicy)
    },
    addFriend (state, newFriend) {
      if (!state.user.friends) state.user.friends = []
      state.user.friends.push(newFriend)
    },
    addBank (state, newBank) {
      if (!state.user.bank_accounts) state.user.bank_accounts = []
      state.user.bank_accounts.push(newBank)
    },
    addAccident (state, newAccident) {
      if (!state.user.accidents) state.user.accidents = []
      state.user.accidents.push(newAccident)
    },
    addTransaction (state, newTransaction) {
      if (!state.user.transactions) state.user.transactions = []
      state.user.transactions.push(newTransaction)
    },
    deletePolicy (state, policy) {
      Vue.delete(state.user.policies, policy.$index)
    },
    deleteFriend (state, friend) {
      Vue.delete(state.user.friends, friend.$index)
    },
    deleteBank (state, bank) {
      Vue.delete(state.user.bank_accounts, bank.$index)
    },
    deleteAccident (state, accident) {
      Vue.delete(state.user.accidents, accident.$index)
    },
    setBanks (state, banks) {
      state.banks = banks
    },
    setInsurances (state, insurances) {
      state.insurances = insurances
    },
    setPolicyTypes (state, PolicyTypes) {
      state.policy_types = PolicyTypes
    },
    setValuePoints (state, values) {
      state.options = values
    },
    selectPolicy (state, policy) {
      state.selectedPolicy = policy
    },
    selectFriend (state, friend) {
      state.selectedFriend = friend
    },
    selectAccident (state, accident) {
      state.selectedAccident = accident
    },
    selectBank (state, bank) {
      state.selectedBank = bank
    },
    clearSelecteds (state) {
      state.selectedPolicy = null
      state.selectedFriend = null
      state.selectedAccident = null
      state.selectedBank = null
    }
  },
  getters: {
    appOnline () {
      return navigator.onLine
    },
    userLogued (state) {
      return state.user.api_token && state.user.api_token.length > 0
    },
    userPhoto (state) {
      return state.user.photo || '/src/assets/images/no-photo.png'
    },
    userCredits (state) {
      let sum = 0
      if (state.user) {
        for (let i = 0; i < state.user.credits.length; i++) {
          sum += state.user.credits[i].credits
        }
      }

      return sum
    }
  },
  actions: {
    login: function (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/login', {email: request.email, password: request.password})
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    getMasters (context) {
      Vue.http.get('/masters', {params: {api_token: context.state.user.api_token}}).then(
        response => {
          if (response.ok) {
            context.commit('setBanks', response.data.banks)
            context.commit('setInsurances', response.data.insurances)
            context.commit('setPolicyTypes', response.data.types)
            context.commit('setValuePoints', response.data.options)
          }
        }
      )
    },
    setRequest (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/add/request', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    setPolicy (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/add/policy', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    setFriend (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/add/friend', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    setAccident (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/add/accident', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    setBankAccount (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/add/bank_account', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    setTransaction (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/add/transfer', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    delPolicy (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/del/policy', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    delFriend (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/del/friend', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    delAccident (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/del/accident', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    delBankAccount (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/del/bank_account', request)
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    }
  },
  plugins: [
    VuejsStorage({
      namespace: 'guiaseguro',
      storage: window.localStorage
    })
  ]
})



