<template>
  <v-card class="friend-dialog">
    <v-dialog v-model="dialog" persistent max-width="500px" min-width="300px">
      <v-card>
        <v-card-title><h2>Confirmar</h2></v-card-title>
        <v-card-text>
          ¿Realmente desea eliminar este amigo?
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click.stop="dialog = false">Cancelar</v-btn>
          <v-btn color="warning" flat @click.stop="deleteFriend">Aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-toolbar dark class="secondary">
      <v-btn icon @click.native="$emit('close-dialog', false)" dark>
        <v-icon>close</v-icon>
      </v-btn>

      <v-toolbar-title>Amigo</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn fab dark flat @click.native="dialog = true">
          <v-icon>delete</v-icon>
          <span class="hidden-sm-and-down">Eliminar</span>
        </v-btn>
        <v-btn fab dark flat :to="{name: 'NewFriend'}">
          <v-icon>mode_edit</v-icon>
          <span class="hidden-sm-and-down">Editar</span>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-card-text>
      <v-layout wrap fill-height>
        <v-flex xs12 class="text-xs-center">
          <h1>{{friend.full_name}}</h1>
          <v-avatar size="200px" tile>
            <img :src="(friend.photo) ? friend.photo: '/src/assets/images/no-photo.png'" :alt="friend.full_name">
          </v-avatar>
        </v-flex>
        <hr>
        <v-flex xs12>
          <ga-status :checks="friendStatus"></ga-status>
        </v-flex>
        <v-flex xs12 class="text-xs-center more-details">
          <h3>
            <table>
              <tr>
                <td><strong>CORREO:</strong></td>
                <td>{{friend.email}}</td>
              </tr>
              <tr>
                <td><strong>TELÉFONO:</strong></td>
                <td>{{friend.phone}}</td>
              </tr>
            </table>
          </h3>
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
  import GaStatus from '../partial/GAStatus'
  import { VAvatar, VDialog } from 'vuetify'
  import { VCard, VCardText, VCardActions, VCardTitle } from 'vuetify/es5/components/VCard'

  export default {
    data () {
      return {
        dialog: false
      }
    },
    computed: {
      friend () {
        return this.$store.state.selectedFriend
      },
      friendStatus () {
        return [
          {label: 'Registrado', checked: (this.friend.status === 'r'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}},
          {label: 'Registro Póliza', checked: (this.friend.status === 'a'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}}
        ]
      }
    },
    methods: {
      deleteFriend () {
        this.dialog = false
        this.$store.commit('deleteFriend', this.friend)
        this.$store.dispatch('delFriend', {friend: this.friend, api_token: this.$store.state.user.api_token})
        this.$store.state.selectedFriend = null
        this.$emit('close-dialog', false)
      }
    },
    components: {
      GaStatus,
      VAvatar,
      VCard,
      VCardText,
      VCardTitle,
      VCardActions,
      VDialog
    }
  }
</script>

<style lang="stylus" scoped>
  h2
    font-weight normal
</style>



