<template>
  <section class="friends-tab">
    <v-dialog v-if="selectedFriend" v-model="dialog" fullscreen scrollable transition="dialog-bottom-transition" :overlay="false">
      <friend @close-dialog="showDialog(arguments[0])"></friend>
    </v-dialog>

    <v-menu offset-y v-model="subMenu.show" :position-absolutely="true" :position-x="subMenu.x" :position-y="subMenu.y">
      <v-list>
        <v-list-tile v-for="item in subMenu.items" :key="item.title" @click.native="subMenuAction(item.method)">
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>

    <v-layout>
      <v-flex class="text-xs-center">
        <img src="/static/friends-top.png" alt="friends" class="top-responsive">
      </v-flex>
    </v-layout>

    <v-layout wrap>
      <div v-if="friends.length < 1">
        <v-flex xs12>
          <h3 class="text-xs-center">¡Empieza a ganar dinero invitando a tus amigos!</h3>
        </v-flex>
        <v-flex xs12>
          <p class="text-xs-center">
            Te otorgaremos un bonos de <strong>Bsf. {{friend_value}}</strong> por cada amigo que registres
            y este obtenga al menos un bono por póliza registrada.
          </p>
        </v-flex>
      </div>
      <v-flex xs12>
        <v-list v-if="friends && friends.length > 0" three-line>
          <template v-for="(friend, index) in friends">
            <v-list-tile :ripple="false"
                         avatar
                         :key="friend.id"
                         @click.native="selectFriend(friend, index)"
            >
              <v-list-tile-avatar>
                <img :src="(friend.photo) ? friend.photo: '/src/assets/images/no-photo.png'"/>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title ><strong>{{friend.full_name}} <small class="grey--text lighten-5">{{(friend.id) ? '' : '● No sincronizado'}}</small></strong></v-list-tile-title>
                <v-list-tile-sub-title v-show="friend.email" v-text="friend.email"></v-list-tile-sub-title>
                <v-list-tile-sub-title v-show="friend.phone" v-text="friend.phone"></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider v-if="index + 1 < friends.length"></v-divider>
          </template>
          <v-list-tile :ripple="false"></v-list-tile>
        </v-list>
        <p v-else class="text-xs-center">
          ¡No tienes <strong>amigos</strong> agregados! usa el botón
          <v-btn color="primary"
                 small
                 fab
                 class="elevation-1"
                 :to="{name: 'NewFriend'}"
          >
            <v-icon>icon-new-friend</v-icon>
          </v-btn>
          para agregar un nuevo amigo
        </p>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import { VDialog, VMenu, VAvatar, VDivider } from 'vuetify'
  import genericTab from './Generic.vue'
  import Friend from '../Dialogs/Friend.vue'

  export default {
    mixins: [genericTab],
    data () {
      return {}
    },
    computed: {
      selectedFriend () {
        return this.$store.state.selectedFriend
      },
      friends () {
        return this.$store.state.user.friends
      },
      friend_value () {
        return this.$store.state.options.point_for_refer
      }
    },
    methods: {
      selectFriend (friend, index) {
        friend.$index = index
        this.$store.commit('selectFriend', friend)
        this.dialog = true
      }
    },
    components: {
      Friend,
      VDialog,
      VMenu,
      VAvatar,
      VDivider
    }
  }
</script>

<style>

</style>



