<template>
  <section class="accidents-tab">
    <v-dialog v-if="selectedAccident" v-model="dialog" fullscreen scrollable transition="dialog-bottom-transition" :overlay="false">
      <accident @close-dialog="showDialog(arguments[0])"></accident>
    </v-dialog>

    <v-menu offset-y v-model="subMenu.show" :position-absolutely="true" :position-x="subMenu.x" :position-y="subMenu.y">
      <v-list>
        <v-list-tile v-for="item in subMenu.items" :key="item.title" @click.native="subMenuAction(item.method)">
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>

    <v-layout>
      <v-flex class="text-xs-center">
        <img src="/static/accidents-top.png" alt="accidents" class="top-responsive">
      </v-flex>
    </v-layout>

    <v-layout wrap>
      <div v-if="items.length < 1">
        <v-flex xs12 class="text-xs-center">
          <h3>¡Estamos para ayudarte!</h3>
          <p>
            Aquí podrás realizar un registro previo de tu incidente,
            asi uno de nuestros expertos se pondrá en contacto contigo para resolver de una manera
            eficiente tu siniestro
          </p>
        </v-flex>
      </div>
      <v-flex xs12>
        <p class="text-xs-center" v-if="!hasPolicies">
          ¡Para registrar un <strong>siniestro</strong> al menos debes tener una <strong>póliza</strong> registrada! usa el botón
          <v-btn color="primary"
                 small
                 fab
                 class="elevation-1"
                 :to="{name: 'NewPolicy'}"
          >
            <v-icon>icon-new-policy</v-icon>
          </v-btn>
          para agregar una nuevo póliza
        </p>
        <v-list three-line v-else-if="items && items.length > 0">
          <template v-show="items.length > 0"  v-for="(item, index) in items">
            <v-list-tile avatar
                         :ripple="false"
                         v-bind:key="item.id"
                         @click.native="selectAccident(item, index)"
            >
              <v-list-tile-content>
                <v-list-tile-title><strong>{{ item.policy.alias }} <small class="grey--text lighten-5">{{(item.id) ? '' : '● No sincronizado'}}</small></strong></v-list-tile-title>
                <v-list-tile-sub-title>{{ item.policy.cod_number | formatCod }} ● {{ item.policy.insurance.name }}</v-list-tile-sub-title>
                <v-list-tile-sub-title><strong>Status: {{ item.status == 'p' ? 'Pendiente por revisión': 'Pagada'}}</strong></v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-avatar>
                <v-icon>{{ item.policy.policy_type.icon}}</v-icon>
              </v-list-tile-avatar>
            </v-list-tile>
            <v-divider v-if="index + 1 < item.length"></v-divider>
          </template>
          <v-list-tile :ripple="false"></v-list-tile>
        </v-list>
        <p v-else class="text-xs-center">
          ¡No tienes siniestros agregados! usa el botón
          <v-btn color="primary"
                 small
                 fab
                 class="elevation-1"
                 :to="{name: 'NewAccident'}"
          >
            <v-icon>icon-new-accident</v-icon>
          </v-btn>
          para registrar un nuevo siniestro
        </p>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import { VDialog, VMenu, VAvatar, VDivider } from 'vuetify'
  import genericTab from './Generic.vue'
  import Accident from '../Dialogs/Accident.vue'

  export default {
    mixins: [genericTab],
    data () {
      return {}
    },
    computed: {
      hasPolicies () {
        if (this.$store.state.user.policies) {
          return this.$store.state.user.policies.length > 0
        } else {
          return false
        }
      },
      selectedAccident () {
        return this.$store.state.selectedAccident
      },
      items () {
        return this.$store.state.user.accidents
      }
    },
    methods: {
      selectAccident (accident, index) {
        accident.$index = index
        this.$store.commit('selectAccident', accident)
        this.dialog = true
      }
    },
    components: {
      Accident,
      VDialog,
      VMenu,
      VAvatar,
      VDivider
    }
  }
</script>

<style>

</style>



