<template>
  <v-card class="policy-dialog">
    <v-dialog v-model="dialog" persistent max-width="500px" min-width="300px">
      <v-card>
        <v-card-title><h2>Confirmar</h2></v-card-title>
        <v-card-text>
          ¿Realmente desea eliminar esta póliza?
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click.stop="dialog = false">Cancelar</v-btn>
          <v-btn color="warning" flat @click.stop="deletePolicy">Aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-toolbar dark class="secondary">
      <v-btn icon @click.native="$emit('close-dialog', false)" dark>
        <v-icon>close</v-icon>
      </v-btn>

      <v-toolbar-title>Póliza</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn fab dark flat @click.native="dialog = true">
          <v-icon>delete</v-icon>
          <span class="hidden-sm-and-down">Eliminar</span>
        </v-btn>
        <v-btn fab dark flat :to="{name: 'NewPolicy'}">
          <v-icon>mode_edit</v-icon>
          <span class="hidden-sm-and-down">Editar</span>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-card-text class="pa-0">
      <v-layout>
        <v-flex>
          <v-card class="grey darken-4 white--text">
            <v-btn color="primary" fab class="policy-type">
              <v-icon>{{policy.policy_type.icon}}</v-icon>
            </v-btn>
            <v-card-text class="white--text">
              <h2><strong>Titular:</strong> {{user.full_name}}.</h2>
              <h3><strong>Nro:</strong> {{policy.cod_number}}.</h3>
              <h3><strong>Compañía:</strong> {{policy.insurance.name}}.</h3>
              <h3><strong>Fecha de vencimiento:</strong> {{policy.end_date ? policy.end_date : 'En proceso...'}}.</h3>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-layout class="pa-3" wrap fill-height>
        <v-flex xs12>
          <ga-status :checks="policyStatus"></ga-status>
        </v-flex>

        <v-flex xs12 v-if="policy.status === 'r'">
          <h3>¡Ya falta poco!</h3>
          <p>Para finalizar el proceso de registro deberás descargar y llenar esta planilla</p>
          <div></div>
          <p>Y cargarla junto a una foto o copia de tu cédula.</p>
          <div></div>
        </v-flex>
        <v-flex xs12 class="more-details" v-else>
          <h3 v-for="(rules, field) in policy.policy_type.fields">
            <table>
              <tr>
                <td><strong>{{field}}:</strong></td>
                <td>En proceso...</td>
              </tr>
            </table>
          </h3>
        </v-flex>

        <v-flex xs12 v-show="policy.status === 'a'">
          <v-btn color="primary" block>Mas detalles</v-btn>
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
  import GaStatus from '../partial/GAStatus'
  import { VAvatar, VDialog } from 'vuetify'
  import { VCard, VCardTitle, VCardText, VCardActions } from 'vuetify/es5/components/VCard'

  export default {
    data () {
      return {
        dialog: false
      }
    },
    computed: {
      user () {
        return this.$store.state.user
      },
      policy () {
        return this.$store.state.selectedPolicy
      },
      policyStatus () {
        return [
          {label: 'Documentos', checked: (this.policy.status === 'r'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}},
          {label: 'Revisión', checked: (this.policy.status === 'r'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'primary', pending: 'primary'}},
          {label: 'Aceptado', checked: (this.policy.status === 'a'), icons: {checked: 'icon-check', unchecked: 'icon-cancel'}, colors: {checked: 'green', unchecked: 'grey darken-1', pending: 'primary'}}
        ]
      }
    },
    methods: {
      deletePolicy () {
        this.dialog = false
        this.$store.commit('deletePolicy', this.policy)
        this.$store.dispatch('delPolicy', {policy: this.policy, api_token: this.user.api_token})
        this.$store.state.selectedPolicy = null
        this.$emit('close-dialog', false)
      }
    },
    components: {
      GaStatus,
      VCard,
      VCardTitle,
      VCardText,
      VCardActions,
      VAvatar,
      VDialog
    }
  }
</script>

<style lang="stylus" scoped>
  h1,h2,h3,h4
    font-weight normal
  .btn.policy-type
    position: absolute !important
    right: 0% !important
    top: -6px !important
</style>



