<template>
  <section class="new-policy-page">
    <ga-simple-header>
      <v-btn slot="backButton" icon :to="{name: 'Main', params: {tab: 'policies'}}" dark>
        <v-icon>close</v-icon>
      </v-btn>
      Contratar Póliza
    </ga-simple-header>

    <v-layout>
      <v-flex class="text-xs-center">
        <img src="/static/contract-policy-top.png" alt="accidents" height="150">
      </v-flex>
    </v-layout>

    <v-layout wrap class="pa-3">
      <v-flex xs12>
        <h3 class="text-xs-center">¡DEJANOS AYUDARTE!</h3>
      </v-flex>
      <v-flex xs12>
        <p class="text-xs-center">
          Queremos brindarte las mejores opciones y el mejor precio,
          además optar por un bono cada vez que contrates una póliza con nosotros
        </p>
      </v-flex>
      <v-form v-model="valid">
        <v-flex xs12>
          <v-select
            v-bind:items="policy_types"
            v-model="request.data.policy_type"
            label="Tipo de póliza"
            item-text="name"
            item-value="id"
            return-object
            :prepend-icon="policyTypeIcon"
            cache-items
            required
            :rules="RequiredRules('El tipo de póliza es obligatorio')"
            autocomplete
          >
            <template slot="item" slot-scope="data">
              <template>
                <v-list-tile-avatar>
                  <v-icon>{{data.item.icon}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </template>
          </v-select>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="request.data.phone"
            label="Teléfono"
            required
            :rules="PhoneRules"
          ></v-text-field>
        </v-flex>
      </v-form>
    </v-layout>

    <div class="text--info">
      Uno de nuestros especialistas se pondrá en contacto contigo
    </div>

    <v-btn
      primary
      fixed
      bottom
      right
      large
      fab
      @click.native="addRequest"
      :disabled="!valid"
    >
      <v-icon x-large>phone</v-icon>
    </v-btn
      primary>

  </section>
</template>

<script>
  import { RequiredRules, PhoneRules } from '../../validator'
  import { VForm, VTextField, VSelect, VAvatar } from 'vuetify'

  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        RequiredRules,
        PhoneRules,
        valid: false,
        request: {
          api_token: this.$store.state.user.api_token,
          type: 'contract policy',
          data: {
            phone: null,
            policy_type: null
          }
        }
      }
    },
    mounted () {
      this.request.data.phone = this.phone
    },
    computed: {
      phone: {
        get () {
          return this.$store.state.user.phone
        },
        set (newValue) {
          this.request.data.phone = newValue
        }
      },
      policy_types () {
        return this.$store.state.policy_types
      },
      policyTypeIcon () {
        return (this.request.data.policy_type) ? this.request.data.policy_type.icon : 'cancel'
      }
    },
    methods: {
      addRequest () {
        this.$store.dispatch('setRequest', this.request).then(
          response => {
            if (response.ok && response.status === 200) {
              this.$router.back()
            }
          }
        )
      }
    },
    components: {
      GaSimpleHeader,
      VForm,
      VTextField,
      VSelect,
      VAvatar
    }
  }
</script>

<style lang="stylus" scoped>
  .text--info
    position: fixed;
    bottom: 24px;
    font-size: 1.4rem;
    width: 80%;
    left: 5%;
</style>



