<template>
  <section class="account-page">
    <ga-simple-header>Mi Cuenta</ga-simple-header>

    <v-layout row wrap>
      <v-flex>
        <v-card>
          <!--Profile Photo-->
          <div class="display-1 text-xs-center text--white">{{ user.full_name }}</div>
          <v-card-media :src="userPhoto" height="200px">
            <v-layout column>
              <v-card-title>
                <v-spacer></v-spacer>
                <v-menu bottom right>
                  <v-btn small fab slot="activator" color="black" dark>
                    <v-icon>mode_edit</v-icon>
                  </v-btn>
                  <v-list>
                    <v-list-tile>
                      <v-list-tile-title @click="editable = !editable">{{(editable) ? 'Cancelar edición' : 'Editar Cuenta'}}</v-list-tile-title>
                    </v-list-tile>
                  </v-list>
                </v-menu>
              </v-card-title>
            </v-layout>
          </v-card-media>
          <!--Profile data-->
          <v-list two-line>
            <v-subheader v-show="editable">Cambiar datos de contacto</v-subheader>
            <v-list-tile v-show="user.phone">
              <v-list-tile-action>
                <v-icon class="black--text">phone</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <span v-if="editable"><v-text-field label="Teléfono" v-model="user.phone"></v-text-field></span>
                <span v-else>{{ user.phone }}</span>
                <v-list-tile-sub-title v-show="!editable">Movile</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset v-show="!editable"></v-divider>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon class="black--text">mail</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <span v-if="editable"><v-text-field label="Correo Electrónico" v-model="user.email" type="email"></v-text-field></span>
                <span v-else>{{ user.email }}</span>
                <v-list-tile-sub-title v-show="!editable">Personal</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>

          <!--Change Password-->
          <v-list v-show="editable">
            <v-subheader>Cambiar contraseña</v-subheader>
            <v-list-tile>
              <v-list-tile-content>
                <v-text-field
                  label="Introduce tu nueva contraseña"
                  hint="Al menos 8 caracteres"
                  v-model="password"
                  min="8"
                  :append-icon="e1 ? 'visibility' : 'visibility_off'"
                  :append-icon-cb="() => (e1 = !e1)"
                  :type="e1 ? 'password' : 'text'"
                  counter
                ></v-text-field>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile>
              <v-list-tile-content>
                <v-text-field
                  label="Repite tu nueva contraseña"
                  hint="Al menos 8 caracteres"
                  v-model="repassword"
                  min="8"
                  :append-icon="e2 ? 'visibility' : 'visibility_off'"
                  :append-icon-cb="() => (e2 = !e2)"
                  :type="e2 ? 'password' : 'text'"
                  counter
                ></v-text-field>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile></v-list-tile>
            <v-list-tile></v-list-tile>
          </v-list>
        </v-card>
        <h5 class="text-xs-center">
          <small class="black--text">Registrado el: {{user.created_at}}</small>
        </h5>
      </v-flex>
    </v-layout>

    <v-btn v-show="editable"
           fixed
           bottom
           right
           large
           fab
           @click.native="save"
    >
      <v-icon>save</v-icon>
    </v-btn>
  </section>
</template>

<script>
  import {
    VMenu,
    VAvatar,
    VDivider,
    VTextField,
    VSubheader
  } from 'vuetify'

  import {
    VCard,
    VCardText,
    VCardTitle,
    VCardMedia
  } from 'vuetify/es5/components/VCard'

  import GaSimpleHeader from '../partial/GaSimpleHeader'

  export default {
    data () {
      return {
        e1: true,
        e2: true,
        editable: false,
        password: null,
        repassword: null
      }
    },
    computed: {
      user () {
        return this.$store.state.user
      },
      userPhoto () {
        return this.$store.getters.userPhoto
      }
    },
    methods: {
      save () {
        this.editable = false
      }
    },
    components: {
      GaSimpleHeader,
      VCard,
      VCardText,
      VCardTitle,
      VCardMedia,
      VMenu,
      VAvatar,
      VDivider,
      VTextField,
      VSubheader
    }
  }
</script>

<style lang="stylus" scoped>
  .list__tile__title
    overflow: hidden
  .container
    padding: 0
</style>



