<template>
  <section class="main-page">
    <v-dialog v-model="dialog">
      <ga-select-policy-dialog @close-dialog="dialog = arguments[0]"></ga-select-policy-dialog>
    </v-dialog>

    <v-toolbar color="secondary" height="72">
      <v-toolbar-title>
        <img src="/src/assets/images/logo-white.png" alt="logo" height="62px">
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu bottom left v-if="notifications && notifications.length > 0">
        <v-badge left>
          <span slot="badge">{{notifications.length}}</span>
          <v-icon slot="activator" dark color="grey">icon-bell</v-icon>
        </v-badge>
        <v-list>
          <v-list-tile v-for="notificacion in notifications" :key="notificacion.title" :to="notificacion.route">
            <v-list-tile-title>{{ notificacion.title }}</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-menu bottom right>
        <v-btn icon slot="activator" dark>
          <v-icon>more_vert</v-icon>
        </v-btn>
        <v-list>
          <v-list-tile v-for="option in options" :key="option.title" :to="option.route">
            <v-list-tile-title>{{ option.title }}</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
       <!--TABS -->
      <v-tabs
        dark
        color="secondary"
        slot="extension"
        icons-and-text
        centered
        fixed-tabs
        v-model="activeTab"
      >
        <v-tab
          v-for="(tab, index) in tabs"
          :key="index"
          :href="'#tab-' + index"
        >
          <small>{{tab.title}}</small>
          <v-icon>{{tab.icon}}</v-icon>
        </v-tab>
      </v-tabs>
    </v-toolbar>

    <v-tabs-items v-model="activeTab">
      <v-tab-item
        v-for="(tab, index) in tabs"
        :key="index"
        :id="'tab-' + index"
      >
        <v-card flat>
          <v-card-text>
            <component :is="tab.component"></component>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>

    <v-footer v-show="!this.$store.getters.appOnline" fixed class="text-sm-center text-md-center text-xs-center text-lg-center">
      Problemas de conexion
    </v-footer>

    <v-fab-transition>
      <v-btn color="primary"
             fixed
             bottom
             right
             large
             fab
             :disabled="activeFab.disabled"
             @click.native="fabAction"
             :key="activeFab.icon"
      >
        <v-icon>{{activeFab.icon}}</v-icon>
      </v-btn>
    </v-fab-transition>
  </section>
</template>

<script>
  import {
    VMenu,
    VTextField,
    VBadge,
    VDialog
  } from 'vuetify'

  import {
    VTabs,
    VTab,
    VTabsItems,
    VTabItem
  } from 'vuetify/es5/components/VTabs'

  import {
    VCard,
    VCardText
  } from 'vuetify/es5/components/VCard'

  import PoliciesTab from '../Tabs/Polices.vue'
  import FriendsTab from '../Tabs/Friends.vue'
  import AccidentsTab from '../Tabs/Accidents.vue'
  import GaSelectPolicyDialog from '../dialogs/SelectPolicy.vue'

  export default {
    data () {
      return {
        dialog: false,
        activeTab: null,
        stickyTab: false,
        tabs: [
          {'title': 'Mis Pólizas', component: PoliciesTab, icon: 'icon-policies', route: 'policies'},
          {'title': 'Mis Amigos', component: FriendsTab, icon: 'icon-friends', route: 'friends'},
          {'title': 'Mis Siniestros', component: AccidentsTab, icon: 'icon-accidents', route: 'accidents'}
        ],
        showOptions: false,
        options: [
          {'title': 'Mi Cartera', 'route': {name: 'Wallet'}},
          {'title': 'Mi cuenta', 'route': {name: 'Account'}},
          {'title': 'Configuración', 'route': {name: 'Config'}},
          {'title': 'Soporte', 'route': {name: 'Support'}},
          {'title': 'Salir', 'route': {name: 'Login'}}
        ]
      }
    },
    created () {
      // Clear Selecteds items
      this.$store.commit('clearSelecteds')
      // Select Tab
      if (this.$route.params.tab) {
        switch (this.$route.params.tab) {
          case 'policies': this.activeTab = 'tab-0'; break
          case 'friends': this.activeTab = 'tab-1'; break
          case 'accidents': this.activeTab = 'tab-2'; break
        }
      }
    },
    methods: {
      onScroll (e) {
        const _H = 40
        let offsetTop = window.pageYOffset || document.documentElement.scrollTop
        if (offsetTop >= _H && !this.stickyTab) {
          this.stickyTab = true
        } else if (offsetTop < _H && this.stickyTab) {
          this.stickyTab = false
        }
      },
      fabAction () {
        if (this.activeFab.route) {
          this.$router.push(this.activeFab.route)
        } else if (this.activeFab.dialog) {
          this.dialog = true
        } else {
          return null
        }
      }
    },
    computed: {
      notifications () {
        return this.$store.state.user.notifications
      },
      activeFab () {
        let canAddAccidents = false
        if (this.$store.state.user.policies) canAddAccidents = this.$store.state.user.policies.length > 0 && this.$store.getters.appOnline

        switch (this.activeTab) {
          case 'tab-0':
            this.$router.replace({name: 'Main', params: {tab: 'policies'}})
            return {icon: 'icon-new-policy', dialog: true, disabled: !this.$store.getters.appOnline}
          case 'tab-1':
            this.$router.replace({name: 'Main', params: {tab: 'friends'}})
            return {icon: 'icon-new-friend', route: {name: 'NewFriend'}, disabled: !this.$store.getters.appOnline}
          case 'tab-2':
            this.$router.replace({name: 'Main', params: {tab: 'accidents'}})
            return {icon: 'icon-new-accident', route: {name: 'NewAccident'}, disabled: !canAddAccidents}
          default:
            return {}
        }
      }
    },
    components: {
      VDialog,
      GaSelectPolicyDialog,
      VMenu,
      VTextField,
      VTabs,
      VTab,
      VTabsItems,
      VTabItem,
      VCard,
      VCardText,
      VBadge
    }
  }
</script>



