<template>
  <v-toolbar :class="classes">
    <slot name="backButton">
      <v-btn icon :to="backTo" v-show="showBackBtn">
        <v-icon class="white--text">arrow_back</v-icon>
      </v-btn>
    </slot>
    <v-toolbar-title v-show="showTitle">
      <slot></slot>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <div class="text-xs-right" v-show="showLogo">
      <img :src="srcLogo" alt="logo" :height="heightLogo">
    </div>
  </v-toolbar>
</template>

<script>
  export default {
    data () {
      return {}
    },
    props: {
      showTitle: {
        type: Boolean,
        default: true
      },
      showLogo: {
        type: Boolean,
        default: true
      },
      showBackBtn: {
        type: Boolean,
        default: true
      },
      backTo: {
        type: String,
        default: 'Main'
      },
      classes: {
        type: String,
        default: 'secondary  white--text'
      },
      srcLogo: {
        type: String,
        default: '/src/assets/images/logo-min.png'
      },
      heightLogo: {
        type: String,
        default: '44px'
      }
    }
  }
</script>



